import prompt from '../../../component/prompt.vue';
export default {
	data() {
		return {
			arr:[
				{id:1,name:"回忆",img:"../../../static/yhr.jpg"},
				{id:2,name:"回忆",img:"../../../static/yhr.jpg"},
				{id:3,name:"回忆",img:"../../../static/yhr.jpg"},
				{id:4,name:"回忆",img:"../../../static/yhr.jpg"},
			],
		}
	},
	components: {
		prompt,
	},
	methods: {
		goToPhoto:function(){
			uni.navigateTo({
				url:"../../../pages/photo/photo",
			})
		},
		goToMy:function(id){
			uni.navigateTo({
				url:"../pages/photoList/photoList?Id="+id,
			})
		},
		shangchuan:function(){
			uni.showModal({
				 title: '提示',
				content: '这是一个模态弹窗',
			success: function (res) {
				if (res.confirm) {
					console.log('用户点击确定');
				} else if (res.cancel) {
					console.log('用户点击取消');
				}
			}
			})
		},
		prompt:function(){
			this.$refs.prompt.show();
		},
		onConfirm:function(e){
			console.log(e);
			let _cost = e;
			if (_cost == '') {
			 console.log('你还未输入');
			 return;
			}
			else{
			  this.$refs.prompt.hide();
			  uni.showModal({
			  	title: '提示',
			  	content: '创建成功',
			  	showCancel: false,
			  	confirmText: "确定",
				success: function (res) {
					if (res.confirm) {
						// 重新加载本页面
						console.log('用户点击确定');
					} else if (res.cancel) {
						console.log('用户点击取消');
					}}
			  })
			 
			}
		},
		onCancel:function(){
			this.$refs.prompt.hide();
			this.$refs.prompt.cost = '';
		}

	}
}
